<template>
        <div class="list_box">
            <div class="searchBox">
                <h3><span @click='goLast' class="icon-left"></span>
                    <p>新增银行卡</p>
                </h3>
            </div>
            <div class="form_one_box addCard">
                <yd-cell-group>
                    <yd-cell-item>
                        <span slot="left">银行名称</span>
                        <yd-input slot="right" v-model="input3" placeholder="请输入银行名称"></yd-input>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left">户&#12288;名</span>
                        <yd-input slot="right" v-model="input4" placeholder="请输入户名"></yd-input>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left">卡&#12288;号</span>
                        <yd-input slot="right" v-model="input5" placeholder="请输入卡号"></yd-input>
                    </yd-cell-item>
                </yd-cell-group>
            </div>
            <div class="wallet_btn">
                <yd-button @click.native="save()" bgcolor="#5e1515" color="#fff" size="large" type="danger">保&#12288;存</yd-button>
            </div>
        </div>
    </template>
    <script>
        import ajax from "../../ajax/ajax.js";
        import dataURL from "../../server/dataUrl.js";
        export default {
            data() {
                return {
                    input1: "中国",
                    input2: "",
                    input3: "",
                    input4: "",
                    input5: "",
                }
            },
            methods: {
                goLast() {
                    this.$router.go(-1);
                },
                linkTo(adr) {
                    this.$router.push({
                        path: adr
                    });
                },
                async save() {
                    if (!this.input3) {
                        this.$dialog.toast({
                            mes: "请输入银行名称",
                            timeout: 1500,
                            icon: "error"
                        });
                        return
                    }
                    if (!this.input4) {
                        this.$dialog.toast({
                            mes: "请输入户名",
                            timeout: 1500,
                            icon: "error"
                        });
                        return
                    }
                    if (!this.input5) {
                        this.$dialog.toast({
                            mes: "请输入卡号",
                            timeout: 1500,
                            icon: "error"
                        });
                        return
                    }
                    let res = await ajax.Post(dataURL.main.createCard, {
                        bank_name: this.input3,
                        account: this.input4,
                        card_no: this.input5,
                        bank_code:"000000"
                    })
                    this.$dialog.toast({
                        mes: "新增成功",
                        timeout: 1500,
                        icon: "success"
                    });
                    this.$router.go(-1);
                }
            }
        }
    </script>


